import React, { useContext } from 'react'
import { Layer, Stage } from 'react-konva'
import { useNavigate } from 'react-router-dom'
import { TemplateContext } from '../../../../../contexts/TemplateContext'
import { stageDimensions } from '../../../../../utils/defaults'
import Elements from '../Elements'

interface Props {
    templateObj: any
    width: number
    height: number
}

const TemplatePreview: React.FC<Props> = ({ templateObj, width, height }) => {
    const navigate = useNavigate()
    const handleSelectTemplate = () => {
        console.log({ templateObj })
        // setTemplateData(templateObj, false)
        // navigate(`/createcard`)
    }

    const StageScale = templateObj?.dimensions?.height ? height / templateObj?.dimensions?.height : 0.5

    return (
        <button
            onClick={handleSelectTemplate}
            className="rounded-md justify-center items-center flex flex-col ">
            <Stage
                {...templateObj.dimensions}
                scaleX={StageScale}
                scaleY={StageScale}
            >
                <Layer
                    listening={false}
                >
                    {templateObj?.variations[0]?.elements?.map((item, index) => (
                        <Elements key={index} konvaItem={item} index={index} />
                    ))}
                </Layer>
            </Stage>

        </button>
    )
}

export default TemplatePreview
